import React from "react"
import Link from "gatsby-link"

export default ({ data }) => {
    const logNode = data.markdownRemark;
    const log = logNode.frontmatter;
    
    console.log(data);

    return (
        <div>
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <h1>
                            {log.title} {" "}
                            <small>{log.date}</small>
                        </h1>
                        <h2>{log.desc}</h2>
                        <h5>Posted By: {log.author}</h5>
                    </div>
                    <div className="col-lg-12">
                        <hr />
                        <div dangerouslySetInnerHTML={{__html: logNode.html}} />
                    </div>
                    <div className="col-lg-12">
                        <Link className="btn btn-primary" to="/logs" role="button">&laquo; Return to Logs</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export const query = graphql`
    query LogPostQuery($path: String!) {
        markdownRemark(frontmatter: {path: {eq: $path}}) {
            html
            frontmatter {
                title
                date(formatString: "MMMM DD, YYYY")
                author
                desc
            }
        }
    }
`


